import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import React from "react";
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import Dropdown from 'react-bootstrap/Dropdown';
import DropdownButton from 'react-bootstrap/DropdownButton';
import Chart from "react-apexcharts";
import { useState, useEffect } from 'react';
import * as XLSX from 'xlsx';
import {Routes,Route,Link} from 'react-router-dom'
import Card from 'react-bootstrap/Card';
import CardGroup from 'react-bootstrap/CardGroup';

function App() {
  const [data, setData] = useState([]);
  const [year, setYear]=useState("");
  const [college, setCollege]=useState("");
  const [depart,setDepart]=useState('');
  const [v,setV]=useState([]);

  var graphLabels=[];
  var graphValues=[];
  var collegeList=[];
  var departList=[];
  // const chartOptions={
  //   chart: {
  //     id: "basic-bar"
  //   },
  //   xaxis: {
  //     categories: []
  //   }
  // }
  const [chartSeries,setChartSeries]=useState([]);
  const [chartOptions,setChartOptions]=useState({chart:{id:'basic-bar'},xaxis:{categories:[]}});
  useEffect(() => {
    // public 폴더 내 example.xlsx 파일의 경로
    const filePath = `${process.env.PUBLIC_URL}/tcarData.xlsx`;

    fetch(filePath)
      .then((response) => response.arrayBuffer())
      .then((buffer) => {
        // 파일 읽기
        const workbook = XLSX.read(buffer, { type: 'buffer' });
        const sheetName = workbook.SheetNames[0];
        const worksheet = workbook.Sheets[sheetName];
        const jsonData = XLSX.utils.sheet_to_json(worksheet);
        
        setData(jsonData);
      });
    

  }, []);

  data.map((item,index)=>{
    collegeList.push(item.단과대)
  });

  collegeList=[...new Set(collegeList)];

  
  data.map((item,index)=>{
    if (item.단과대==college)
      departList.push(item.학과)
  });
  
  departList=[...new Set(departList)];
  if(college!=''&&depart!=''){
    if(depart=='full'){
      departList.map((item)=>{
        var avg=0.0;
        var index=1;
        data.filter((e)=> e.학과==item).map((score,idx)=>{
          avg=parseFloat(avg)+parseFloat(score[year].toFixed(3));
          index=idx+1

          
        });
        avg=parseFloat(avg)/parseFloat(index);
        graphLabels.push(item);
        graphValues.push(avg.toFixed(3));
      })
    }else{

      data.map((item)=>{
        if(item.학과==depart){
          graphLabels.push(item.이름);
          graphValues.push(item[year].toFixed(3));
                  }
         });
      }
    console.log(graphLabels);
    console.log(graphValues);
    
    }

  return (
    <div>


      <Navbar1/>

      <Routes>
        <Route path="/" element={
          <div>


      <InputGroup>
        <Form.Select onChange={(e)=>{setYear(e.target.value);setCollege('');setDepart('');}} aria-label="Default select example">
          <option>년도</option>
          <option value="2020">2020년</option>
          <option value="2021">2021년</option>
          <option value="2022">2022년</option>
        </Form.Select>
        <Form.Select onChange={(e)=>{setCollege(e.target.value);setDepart('');}} disabled={year==''?true:false} aria-label="Default select example">
          <option>단과대학</option>
          {
            collegeList.map((item,index) => {
              return(
                <option value={item}>{item}</option>
              )
            })
          }
        </Form.Select>


        <Form.Select onChange={(e)=>{
          setDepart(e.target.value);
          
         }} disabled={college==''?true:false} aria-label="Default select example">
          <option>학과</option>
          <option value='full'>전체 분석</option>
          {
            departList.map((item,index) => {
              return(
                <option value={item}>{item}</option>
              )
            })
          }
        </Form.Select>
      </InputGroup>
     <h1>{year}{college}{depart}</h1>
      
<br/>
      <Chart 
        options={chartOptions}
        series={chartSeries}
        type="bar"
        width="100%"
        height="500"
        
        />
        <button onClick={()=>{setChartSeries([{
            name: "series-1",
            data: [...graphValues]
          }]);
          setChartOptions({
            chart: {
              id: "basic-bar"
            },
            xaxis: {
              categories: [...graphLabels]
            }
          })}}>그래프 업데이트</button>
      {/* <Graph1 depart={depart} labels={v} values={graphValues}/> */}


      </div>

        }/>
        <Route path="/formula" element={
         <div>
          <Card body className="text-center">TCAR = CAA + SAA + LAA + COAA</Card>

          <CardGroup>
            <Card>
             
              <Card.Body>
                <Card.Title className="text-center">CAA</Card.Title>
                <Card.Text>
                  This is a wider card with supporting text below as a natural lead-in
                  to additional content. This content is a little bit longer.
                </Card.Text>
              </Card.Body>
            </Card>
            <Card>
              
              <Card.Body>
                <Card.Title className="text-center">SAA</Card.Title>
                <Card.Text>
                  This is a wider card with supporting text below as a natural lead-in
                  to additional content. This content is a little bit longer.
                </Card.Text>
                
      
              </Card.Body>
            </Card>
            <Card>
              
              <Card.Body>
                <Card.Title className="text-center">LAA</Card.Title>
                <Card.Text>
                  This is a wider card with supporting text below as a natural lead-in
                  to additional content. This content is a little bit longer.
                </Card.Text>
                <Form>
                  <Form.Check 
                      type={'radio'}
                      id={`default-radio`}
                      name='g1'
                      label={`1.1`}
                      Select
                    />
                    <Form.Check 
                      type={'radio'}
                      id={`default-radio`}
                      name='g1'
                      label={`1.3`}
                      Select
                    />
                    <Form.Check 
                      type={'radio'}
                      id={`default-radio`}
                      name='g1'
                      label={`1.5`}
                      Select
                    />
                </Form>
              </Card.Body>
            </Card>
            <Card>
              
              <Card.Body>
                <Card.Title className="text-center">COAA</Card.Title>
                <Card.Text>
                  This is a wider card with supporting text below as a natural lead-in
                  to additional content. This content is a little bit longer.
                </Card.Text>
              </Card.Body>
            </Card>
           </CardGroup>
         </div> 
          
        }/>
        <Route path="/data" element={
          <div>
            <Card body>
              <Card.Title>엑셀 파일 업로드</Card.Title>
            <Form.Control type="file" />
            </Card>
            <Card body>
              <Card.Title>학과 제거</Card.Title>
              <Form.Check 
                type={'switch'}
                id={`default-switch`}
                label={`의과대학`}
                Select
              />
              <Form.Check 
              type={'switch'}
              id={`default-switch`}
              label={`음학대학`}
              Select
            />
            <Form.Check 
                type={'switch'}
                id={`default-switch`}
                label={`예술체육대학`}
                Select
              />

            </Card>
          </div>  
        
        }/>
      </Routes>
    </div>
  );
}


function Navbar1() {
  return (
    <>
      <Navbar bg="primary" data-bs-theme="dark">
        <Container>
          <Navbar.Brand href="#home">TCAR</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link href="/">데이터 분석</Nav.Link>
            <Nav.Link href="/formula">수식 변경</Nav.Link>
            <Nav.Link href="/data">데이터 변경</Nav.Link>
          </Nav>
        </Container>
      </Navbar>
      
    </>
  );
}

class Graph1 extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      options: {
        chart: {
          id: "basic-bar"
        },
        xaxis: {
          categories: this.props.labels
        }
      },
      series: [
        {
          name: "series-1",
          data: this.props.values
        }
      ]
    };
  }

  render() {
    return (
      <div className="app">
        <div className="row">
          <div className="mixed-chart">
            <Chart
              options={this.state.options}
              series={this.state.series}
              type="bar"
              width="500"
            />
          </div>
        </div>
      </div>
    );
  }
}
export default App;